@font-face {
  font-family: 'dash';
  src: url('../font/dash.eot?44667202');
  src: url('../font/dash.eot?44667202#iefix') format('embedded-opentype'),
       url('../font/dash.woff2?44667202') format('woff2'),
       url('../font/dash.woff?44667202') format('woff'),
       url('../font/dash.ttf?44667202') format('truetype'),
       url('../font/dash.svg?44667202#dash') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
